html {
  color-scheme: light dark;
  box-sizing: border-box;
  width: 100vw;
  height: 100svh;
  font-family: sans-serif;
}

@media (prefers-color-scheme: dark) {
  html {
    --autocomplete-background-color: #515151;
    --match-color: cyan;
    --autocomplete-color: white;
    --selection-background-color: #028e8e;
  }
}

@media (prefers-color-scheme: light) {
  html {
    --autocomplete-background-color: #ece6d9;
    --match-color: #028e8e;
    --selection-background-color: #9ff;
    --autocomplete-color: black;
  }

  .cm-editor {
    background-color: #fff !important;
  }

  .cm-editor.ͼ1n .cm-gutters {
    background-color: var(--autocomplete-background-color) !important;
  }
}

.cm-editor ::selection {
  background-color: var(--selection-background-color) !important;
}

.cm-tooltip-autocomplete {
  background-color: var(--autocomplete-background-color) !important;
  color: var(--autocomplete-color) !important;
}

.cm-completion {
  padding: 2px 8px;
}

.cm-completion.cm-active {
  color: var(--autocomplete-color) !important;
}

.cm-completionMatchedText {
  color: var(--match-color);
  font-weight: bold;
}

body {
  box-sizing: border-box;
  flex-direction: column;
  align-content: stretch;
  width: 100vw;
  height: 100svh;
  margin: 0;
  display: flex;
}

body > header {
  margin-left: env(titlebar-area-x, 0);
  margin-top: env(titlebar-area-y, 0);
  height: env(titlebar-area-height, 2rem);
  width: env(titlebar-area-width, 100%);
  align-items: center;
  gap: .5rem;
  display: flex;
}

body > header h1 {
  -webkit-app-region: drag;
  margin: 0;
  font-size: 1rem;
}

#windowTitle {
  -webkit-app-region: drag;
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  flex: 1 1 0;
  overflow: hidden;
}

#abrirArchivo {
  anchor-name: --abrirArchivo;
}

#archivo {
  position-anchor: --abrirArchivo;
  top: 3.5rem;
  top: anchor(bottom);
  left: .5rem;
  left: anchor(left);
  margin: unset;
  transition-behavior: allow-discrete;
  transition-property: overlay, display, transform;
  transition-duration: 1s;
  position: absolute;
  transform: translateY(-50%)scaleY(0);
}

#archivo:popover-open {
  transform: translateY(0);
}

@starting-style {
  #archivo:popover-open {
    transform: translateY(-50%)scaleY(0);
  }
}

#archivo ul {
  padding: 0;
  list-style-type: none;
}

#archivo ul li {
  margin-block: 1rem;
}

.error {
  color: red;
}

#lblabrir {
  cursor: pointer;
}

#abrir {
  position: absolute;
  top: -1000rem;
}

#content {
  box-sizing: border-box;
  flex-grow: 1;
  align-content: stretch;
  margin: 0;
  display: flex;
}

iframe {
  box-sizing: border-box;
  flex: 1 1 0;
  min-width: 0;
  overflow: auto;
}

#code {
  box-sizing: border-box;
  flex-direction: column;
  flex: 1 1 0;
  display: flex;
  overflow: auto;
}

#consoleSec {
  box-sizing: border-box;
  flex: 1 1 0;
  overflow: auto;
}

#code > :nth-child(2) {
  box-sizing: border-box;
  border-bottom: 1px solid #888;
  flex-grow: 1;
  font-size: 1rem;
}
